.App {
  min-height: 100vh;
  background-color: white;
}

ul {
  border: 0;
  margin: 0;
  list-style-type:none;
}

h1, h2, h3, h4, p
{
  margin: 0.4em 0;
}

h1, h2, h3, h4
{
  margin-top: 0.2em;
}

.pane
{
  border: 0.6em solid #fff;
  box-shadow: #00000016 0em 0em 0.5em 0.25em;
  margin: 0.6em;
  flex-grow: 1;
}

.menu
{
  position: absolute;
  top: 50vh;
  transform: translateY(-98%);
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  padding: 0 0.4em;
  left: 0;
  width: 66vw;
}

.blogpost
{
  max-width: 48em;
  padding: 0 0.2em;
}

.blogpost img[alt=small]
{
  width: 12em;
  float: right;
}

.blogpost img[alt=icon]
{
  width: 4em;
}

.blogpost img[alt=full]
{
  width: 100%;
}

.row
{
  width: 100vw;
  flex-direction: row;
  display: flex;
  justify-content: center;
  justify-self: center;
}

.page
{
  display: 'flex';
  align-items: 'center';
  justify-content: 'flex-start';
  flex-wrap: 'wrap'
}

.postcard
{
  width: 400px;
  position: relative;
}

.postcard img
{
  width: 100%;
  height: 100%;
  object-fit:cover;
  vertical-align: top;
}

.postcard div
{
  width: 100%;
  height: 100%;
  box-shadow: inset #222a 0 0 2.0em 0.1em;
  position: absolute;
  top: 0;
}

.postcardtext
{
  position: absolute;
  top: 33%;
  left: 33%;
  width: 60%;
  transform: translate(-50%, -50%) rotate(-30deg);
  text-align: center;
  font-family: 'Permanent Marker';
  display: flex;
  align-items: center;
  background-color: blue;
  justify-content: center;
}

.footer
{
  background-color: #ffbfbf;
  /*box-shadow: inset #ffaaaa 0 0.5em 0.5em 0.1em;*/
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin-top: 1.2em;
  /*font-family: 'Permanent Marker';*/
}

.footer p,a
{
  margin: 0.16em 0;
}

.tab
{
  text-decoration: none;
  color: white;
  flex: 1;
  border: 1px solid #ffaaaa;
  border-bottom: none;
  box-shadow: inset #ffaaaa 0 0 0.5em 0.2em;
  margin: 0 0.2em;
  padding: 0 0.2em;
  text-align: center;
  font-family: 'Permanent Marker', 'Roboto';
  cursor: pointer;
}

.current
{
  color: #ffbfbf;
  background-color: white;
  box-shadow: none;
  transform: scale(1.04);
}

.button
{
  text-decoration: none;
  color: #888;
  padding: 0.2em;
  font-weight: 100;
  box-sizing: border-box;
}

.button:hover
{
  text-decoration: none;
  box-shadow: inset #4441 0 0 0.5em 0.25em;
  color: #ffbfbf;
}

.App-header {
  background-color: #ffbfbf;
  height: 50vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  margin-bottom: 0.6em;
  border-bottom: solid 1px #ffaaaa;
}

/*.App-header:hover
{
  box-shadow: inset #ffbbcc 0 0 0.5em 0.25em;
}*/

.avatar
{
  flex-grow: 0;
  width: 16em;
  height: 16em;
  object-fit: cover;
  border: 0.5em solid white;
  border-radius: 9999px;
  position: absolute;
  top: 50vh;
  right: 0;
  margin: 0;
  padding: 0;
  transform: translate(-10%, -50%);
}

.handles
{
  position: fixed;
  top: 50vh;
  transform: translateY(-50%);
  right: 0;
  margin: 0 0.4em;
  display: flex;
  flex-direction: column;
}

.stamp
{
  width: 4em;
  margin: 1em;
  position: absolute;
  top: 0;
  right: 0;
}


.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.profile
{
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  /*background-color: #f0f4f8;*/
}

.footer a
{
  color: blueviolet;
  text-decoration: underline;
  cursor: pointer;
}

.socialrow
{
  display: flex;
  flex-direction: row;
  align-items: center;
  text-align: left;
  justify-content: flex-end;
}

.socialrow p
{
  margin-right: 0em;
  background-color: #efefef;
  flex-grow: 1;
  padding: 0 0.16em;
  color: #555;
  visibility: hidden;
}

@media only screen and (max-width: 1024px)
{
  .App-header
  {
    max-height: 10em;
  }

  h1
  {
    font-size: 2.3rem;
  }

  .stamp
  {
    max-width: 2.2em;
    margin: 0.5em;
  }

  .menu
  {
    width: 100vw;
    top: 10em;
    box-sizing: border-box;
  }

  .avatar
  {
    position: relative;
    top: initial;
    right: initial;
    transform: initial;
    margin: 0.4em 0;
    width: 10em;
    height: 10em;
  }

  .socialrow p
  {
    visibility: visible;
  }

  .socialrow div
  {
    border: 0.2em solid #efefef;
    border-radius: 9999px;
    box-sizing: border-box;
    transform: translateX(-0.16em);
  }

  .blogpost
  {
    max-width: initial;
    width: 100vw;
  }

  .handles
  {
    position: relative;
    top: initial;
    right: initial;
    transform: initial;
    /*box-shadow: inset #efefef 0 0 1em 0.5em;
    padding: 0.2em;
    border: solid 1px #eee;*/
  }
}
